import { Component } from '@angular/core';

import { Platform, ToastController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { CookieService } from 'ngx-cookie-service';



import { Device } from '@ionic-native/device/ngx';
import { Storage } from '@ionic/storage';
import { AppMin, myLocalStorage } from '../app/core';


import { LocalNotifications } from '@ionic-native/local-notifications/ngx';





@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent {
  UserId;
  userimg;
  phonenumber;
  backButtonPressed: boolean = false;  //用于判断返回键是否触发  
  //rootPage: any = TabsPage;
  device = new Device();

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    public CookieService: CookieService,
    public localNotifications: LocalNotifications,
    public toastCtrl: ToastController,
    public appmin?: AppMin,
    public storage?: Storage,

    // private backgroundMode?: BackgroundMode
  ) {
    this.setscreen();

    this.initializeApp();

    document.addEventListener("pause", function () {
      console.log("应用从前台切换到后台");
      // alert('应用从前台切换到后台')

    }, false);
    document.addEventListener("resume", function () {
      console.log("应用从后台切换到前台");
      // alert('应用从后台切换到前台')
      //  this.appCache();
    }, false);




    ////////////设置初始登录变量↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓

    this.UserId = myLocalStorage.getItem("UserId");
    this.userimg = myLocalStorage.getItem("userimg");
    this.phonenumber = myLocalStorage.getItem("phonenumber");


    console.log("this.UserId=" + this.UserId)
    if (this.UserId != null && this.UserId != '') {

      if (this.phonenumber != null && this.phonenumber != '') {
        //用户已经登录 设置初始变量
        //    WitePrompt("app页面:用户存在登录")

        //判断登录是否过期
        this.appmin.post({
          type: "checkLogin",

        }).subscribe((data) => {
          if (data["islogin"] == 0) {
            console.log("登录过期");
            this.appmin.setLogin(false);
          }else{
            console.log("登录有效");
            this.appmin.setLogin(true);
            this.appmin.setUserId(this.UserId);
            this.appmin.setUserPhoneNumber(this.phonenumber);
            if (this.userimg != null) {
              this.appmin.setUserImg(this.userimg);
            }

          }

        })

      

       

      }
      else {
        this.clearLogin();
      }

    } else {
      this.clearLogin();
    }



    //////////设置初始登录变量end↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑

    // let device: any;
    // document.addEventListener("deviceready", onDeviceReady, false);
    // function onDeviceReady() {
    //   alert(device.cordova);
    // }


    platform.ready().then(() => {
      this.statusBar.overlaysWebView(true);
      this.statusBar.styleBlackTranslucent();
      //  alert(" platform.ready()就绪")



      this.splashScreen.hide();//加载完毕关闭过度窗口

      var modelName: string = this.device.model;//获取设备名
      if (modelName != null) {


        if (modelName.indexOf("iPhone") >= 0) {
          //苹果手机设置

          this.appmin.SetPhoneMode("ios");

          statusBar.show();
          statusBar.overlaysWebView(true);
          statusBar.styleBlackTranslucent();

        }
        else {
          //安卓手机设置
          this.appmin.SetPhoneMode("android");

          // alert("当前为安卓手机" + modelName);

          //设置导航栏样式

          statusBar.overlaysWebView(true);
          statusBar.styleDefault();
          statusBar.styleBlackTranslucent();
        }
      }




      //////////////////本地通知测试
      // this.localNotifications.schedule({
      //   title: '私信通知',
      //   text: '慕碗碟：“有的 vyxie。。”',
      // });
    });


    splashScreen.hide();//加载完毕关闭过度窗口


    //警告 如果发现任何插件不起作用 则需要重新安装插件 安装之前不需要删除插件

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }
  clearLogin() {
    // alert('clearLogin登录失败');
    localStorage.clear();

    this.appmin.setUserId("0");
    this.appmin.setLogin(false);//设为未登录状态

    //this.TabsPage1.setTabs(false);
    console.log("app页面:用户没有登录过")

    //设置设备Id
    this.appmin.setDeviceId(this.device.uuid);
    //alert('当前设备Id为='+ this.device.uuid);

    //假设设备id
    this.appmin.setDeviceId('sd8s865d6d5s4d5l');
    this.splashScreen.hide();//加载完毕关闭过度窗口
  }
  appCache() {
    // this.appmin.ret_login();
    // alert('切制前台');
  }
  setscreen() {
    //适配屏幕尺寸
    if (window.screen.width >= 320) {
      document.getElementsByTagName('html')[0].style.fontSize = '10px';

    }
    if (window.screen.width >= 360) {
      document.getElementsByTagName('html')[0].style.fontSize = '11px';

    }
    if (window.screen.width >= 400) {
      document.getElementsByTagName('html')[0].style.fontSize = '12px';

    }
    if (window.screen.width >= 450) {
      document.getElementsByTagName('html')[0].style.fontSize = '13px';

    }
    if (window.screen.width >= 500) {
      document.getElementsByTagName('html')[0].style.fontSize = '14px';

    }
    if (window.screen.width >= 550) {
      document.getElementsByTagName('html')[0].style.fontSize = '15px';

    }
    if (window.screen.width >= 600) {
      document.getElementsByTagName('html')[0].style.fontSize = '16px';

    }
    if (window.screen.width >= 650) {
      document.getElementsByTagName('html')[0].style.fontSize = '17px';

    }
    if (window.screen.width >= 700) {
      document.getElementsByTagName('html')[0].style.fontSize = '18px';

    }
    if (window.screen.width >= 800) {
      document.getElementsByTagName('html')[0].style.fontSize = '20px';

    }



  }
  //返回按键处理  
}
